    'feature': '功能',
    'chat': '对话',
    'send': '发送',
    'talkMe': '对我说',
    'helloText': '你好，有什么可以帮您',
    'ok': '知道了',
    'thinking': '正在思考',
    'deleteAll': '清空',
    'dayStrs': ['星期日', '星期一', '星期二', '星期三', '星期四', '星期五', '星期六'],
    'focusthing': "焦点事项",
