import '../assets/css/assistant.css'
import TextInput from './head/eyes/TextInput';
import { brainStoreType, brainStoreUtil, brainThinkUtil } from '../assets/js/head/brainUtil';
import { useEffect, useState } from 'react';
import { commonUtil } from '../assets/js/common/commonUtil';
import { i18nUtil } from '../assets/js/common/i18nUtil';
import assistantImg from "../assets/img/assistant.png"
import { HistogramOutline } from 'antd-mobile-icons';
import { mouthUtil } from '../assets/js/head/mouthUtil';
import ReactMarkdown from 'react-markdown';
import SyntaxHighlighter from "react-syntax-highlighter/dist/esm/default-highlight";
import { dark } from "react-syntax-highlighter/dist/esm/styles/hljs";
function XAssistantIndex() {
    const [answerContent, setAnswerContent] = useState("");
    const [askContent, setAskContent] = useState("");
    const [showAnswer, setShowAnswer] = useState(false);
    const [thinkStatus, setThinkStatus] = useState(false);
    const [chatRecords, setChatRecords] = useState([]);
    const [showAskRecord, setShowAskRecord] = useState(false);
    async function handleSendText(sendText) {
        if (!sendText || sendText === '') {
            return;
        }
        // 显示用户问题
        setAskContent(sendText);
        // 本地处理用户问题
        setAnswerContent(i18nUtil.getText("thinking") + "...");
        let localThinkRes = brainThinkUtil.localHandleText(sendText);
        setShowAnswer(true);
        if (localThinkRes && localThinkRes !== '') {
            // 存储对话
            const userMsgId = brainStoreUtil.save(brainStoreType.CHAT_LIST, { from: 'user', content: sendText });
            brainStoreUtil.save(brainStoreType.CHAT_LIST, { from: 'assistant', content: localThinkRes, userMsgId: userMsgId });
            // 更新页面对话
            setAnswerContent(localThinkRes);
            initData();
        } else {
            // 云处理用户消息
            setThinkStatus(true);
            let netThinkRes = await brainThinkUtil.netHandleText(sendText);
            // 存储对话
            const userMsgId = brainStoreUtil.save(brainStoreType.CHAT_LIST, { from: 'user', content: sendText });
            let loadingChatData = { from: 'assistant', userMsgId: userMsgId };
            if (netThinkRes && netThinkRes !== '') {
                loadingChatData.content = netThinkRes;
                loadingChatData.md = true;
            } else {
                loadingChatData.content = "不好意思，我还没学会";
            }
            brainStoreUtil.save(brainStoreType.CHAT_LIST, loadingChatData);
            // 更新页面对话
            setThinkStatus(false);
            setAskContent(sendText);
            setAnswerContent(loadingChatData.content);
            setShowAnswer(true);
            loadChatRecords();
            setTimeout(function () {
                commonUtil.scrollToTop('chatAnswer');
            }, 10);
        }
    }
    function loadChatRecords() {
        let chatList = brainStoreUtil.getDataList(brainStoreType.CHAT_LIST);
        let chatRecords = chatList.filter(chatItem => chatItem.from === 'user');
        chatRecords = commonUtil.sortArray(chatRecords, "createTime", false);
        setChatRecords(chatRecords);
    }
    function showChatRecord(record) {
        let chatList = brainStoreUtil.getDataList(brainStoreType.CHAT_LIST);
        let chatAnswer = chatList.filter(chatItem => chatItem.userMsgId === record.dataId)[0];
        if (!chatAnswer) {
            chatAnswer = { content: i18nUtil.getText('thinking') + "..." };
        }
        setAskContent(record.content);
        setAnswerContent(chatAnswer.content);
        setShowAnswer(true);
        setTimeout(function () {
            commonUtil.scrollToTop('chatAnswer');
        }, 10);
    }
    function loadNotification() {
        const notificationDataTypes = [brainStoreType.TODO_LIST];
        // 初始化通知定时器
        for (const dataType of notificationDataTypes) {
            const dataList = brainStoreUtil.getDataList(dataType);
            for (const data of dataList) {
                // 跳过已完成
                if (data.ok) {
                    continue;
                }
                if (data.okDates && data.okDates.indexOf(new Date().toLocaleDateString()) !== -1) {
                    continue;
                }
                // 重复的计算时间点
                if (data.repeatType) {
                    const nowDate = new Date();
                    const timeStrSplit = data.timeStr.split(":");
                    switch (data.repeatType) {
                        case 'everyday':
                            data.timestamp = new Date(nowDate.getFullYear(), nowDate.getMonth(), nowDate.getDate(), timeStrSplit[0], timeStrSplit[1], 0).getTime();
                            break;
                        case 'workday':
                            if (nowDate.getDay() !== 0 && nowDate.getDay() !== 6) {
                                data.timestamp = new Date(nowDate.getFullYear(), nowDate.getMonth(), nowDate.getDate(), timeStrSplit[0], timeStrSplit[1], 0).getTime();
                            }
                            break;
                        case 'restday':
                            if (nowDate.getDay() === 0 || nowDate.getDay() === 6) {
                                data.timestamp = new Date(nowDate.getFullYear(), nowDate.getMonth(), nowDate.getDate(), timeStrSplit[0], timeStrSplit[1], 0).getTime();
                            }
                            break;
                        case 'sat':
                            if (nowDate.getDay() === 6) {
                                data.timestamp = new Date(nowDate.getFullYear(), nowDate.getMonth(), nowDate.getDate(), timeStrSplit[0], timeStrSplit[1], 0).getTime();
                            }
                            break;
                        case 'sun':
                            if (nowDate.getDay() === 0) {
                                data.timestamp = new Date(nowDate.getFullYear(), nowDate.getMonth(), nowDate.getDate(), timeStrSplit[0], timeStrSplit[1], 0).getTime();
                            }
                            break;
                        default: ;
                    }
                }
                if (!data.timestamp) {
                    continue;
                }
                const endMils = data.timestamp - new Date().getTime();
                if (endMils > 0) {
                    // 定时提醒
                    setTimeout(function () {
                        let content = "现在是 " + new Date(data.timestamp).toLocaleTimeString() + " 你该" + data.title + "了";
                        if (!handleAlertData(dataType, data, content)) {
                            return;
                        }
                        setTimeout(() => {
                            commonUtil.scrollToLeft("assistant-face-talk");
                        }, 1000);
                    }, endMils);
                } else if (endMils <= 0) {
                    let content = "您有一条待办 " + new Date(data.timestamp).toLocaleTimeString() + " " + data.title;
                    let handleAlertDataRes = handleAlertData(dataType, data, content);
                    if (!handleAlertDataRes) {
                        continue;
                    }
                }
            };
        }
        setTimeout(() => {
            commonUtil.scrollToLeft("assistant-face-talk");
        }, 1000);
    }
    function handleAlertData(dataType, data, content) {
        // 已删除，不提醒
        let newestData = brainStoreUtil.getById(dataType, data.dataId);
        if (!newestData) {
            return false;
        }
        if (newestData.ok) {
            return false;
        }
        if (newestData.okDates && newestData.okDates.indexOf(new Date().toLocaleDateString()) !== -1) {
            return false;
        }
        // 系统通知
        mouthUtil.backTalk(content);
        // 本地通知
        // 更新 talkList
        setAnswerContent(content);
        setShowAnswer(true);
        newestData.ok = true;
        brainStoreUtil.updateById(dataType, newestData);
        return true;
    }
    function initData() {
        loadChatRecords();
        loadNotification();
        console.log(thinkStatus);
        console.log(askContent);
    }
    useEffect(function () {
        initData();
        const handleVisibilityChange = () => {
            if (document.visibilityState === 'visible') {
                initData();
            }
        };

        document.addEventListener('visibilitychange', handleVisibilityChange);

        return () => {
            document.removeEventListener('visibilitychange', handleVisibilityChange);
        };
        // eslint-disable-next-line
    }, []);
    return (
        <div className='assistant-index'>
            <div className='assistant-index-main'>
                <div className="data-center"><HistogramOutline /></div>
                <div className='assistant flex-center' onClick={() => {
                    setShowAnswer(false);
                    setAnswerContent('');
                    setAskContent('');
                }}>
                    <img src={assistantImg} alt='' />
                </div>
                <div className='assistant-talk flex-center' style={{ display: showAnswer ? undefined : 'none' }}>
                    <div className='assistant-talk-content'>
                        {/* <div className='assistant-talk-ask'>{askContent}</div> */}
                        <div className='assistant-talk-answer'>
                            <ReactMarkdown
                                children={answerContent}
                                components={{
                                    code: ({ language, children }) => (
                                        <SyntaxHighlighter language={language} style={dark}>
                                            {children}
                                        </SyntaxHighlighter>
                                    )
                                }} />
                        </div>
                    </div>
                </div>
            </div>
            <div className='assistant-index-bottom'>
                <TextInput
                    show={true}
                    chatRecords={chatRecords}
                    loadChatRecords={() => loadChatRecords()}
                    showChatRecord={(record) => showChatRecord(record)}
                    showAskRecord={showAskRecord}
                    setShowAskRecord={(value) => setShowAskRecord(value)}
                    handleSendText={(sendText) => {
                        handleSendText(sendText);
                    }} />
            </div>
        </div>
    );
}
export default XAssistantIndex;